body {
  margin: 0;
  padding: 0;
  font: 13px Menlo,Monaco,Lucida Console,Liberation Mono,DejaVu Sans Mono,Bitstream Vera Sans Mono,Courier New,monospace,serif
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
  padding-bottom: 50px;
}

.header i {
  color: #22BAD9;
  font-size: 12px;
}

.header span {
  color: #22BAD9;
  font-size: 12px;
}

.header i:hover {
  color: #fff;
  background: #22BAD9;
}

.header span:hover {
  color: #fff;
  background: #22BAD9;
}
.homeContent{
  width: 400px;
  height: 100px;
  margin-left: -200px;
  position: absolute;
  top: 40%;
  left: 50%;
  text-align: center;
}

.contentTitle {
  width: 100%;
  height: 40px;
  line-height: 40px;
}

.typeContainer {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 10px 0 0 0;
  padding: 0;
}

.typeList a {
  font-size: 12px;
  color: #22BAD9;
}

.typeList a:hover{
  color: #fff;
  background: #22BAD9;
  cursor: pointer;
}
.listContainer{
  width: 100%;
  overflow: hidden;
}

.listContainer ul {
  width: 100%;
  overflow: hidden;
  padding: 0 ;
}

.listContainer ul li {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-bottom: 10px;
}

.listContainer ul li span {
  width: 140px;
  text-align: right;
  margin-right: 30px;
  color:rgb(153, 153, 153);
  font-size: 14px;
}

.listContainer ul li a {
  color: #22BAD9;
  font-size: 14px;
}

.listContainer ul li a:hover {
  color: #fff;
  background: #22BAD9;
}
.commentList {
  max-width: 650px;
  font-size: 12px;
  margin: 20px auto;
}

.commentList div {
  width: 100%;
  min-height: 120px;
  line-height: 24px;
  overflow: hidden;
}

.commentList i {
  line-height: 24px;
  color: #777777;
}

.commentList p {
  margin-top: 20px;
  color: #333333;
  white-space: pre-wrap;
  font:16px Menlo,Monaco,Lucida Console,Liberation Mono,DejaVu Sans Mono,Bitstream Vera Sans Mono,Courier New,monospace,serif;
}


.commentButton {
  background-color:#22BAD9!important;
  border-color: #22BAD9!important; 
}

.commentButton * {
  color: #fff;
}

.commentRelease {

}

.releaseIcon {
  position: fixed;
  border: 1px solid #22BAD9;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  justify-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  right: calc((100% - 650px)/2);
  bottom: 40px;
}
.article {
  max-width: 650px;
  font-size: 14px;
  margin: 20px auto;
}

.article h1 {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
  color: #FF001F;
}

.article > p {
  margin-bottom: 20px;
  color: #777;
}

.article div > p {
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 24px;
}
*{
  margin: 0;
  font-size: 14px;
  color: #333;
}

a{
  text-decoration: none;
}

li{
  list-style: none;
  list-style-type: none;
}

.pageContainer{
  padding:25px 50px;
}

.commentContainer {
  padding:25px 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100%;
}
